import TM from "../image/TM.jpg";

const Card = ({ title, content }) => (
  <div className="card">
    <div className="header">{title}</div>
    <div className="content">
      {content}
    </div>
  </div>
);

const FScontent = () => (
  <div>
    <div className="header">  (Free Safety) </div>
    <div className="content">
      {" "}
        ,     . 
                
        1:1       , 
             . 
                     , 
      -2  -3       .
    <div className = "star">
      <font size = "5"> Star Player : Tyrann Mathieu</font><br/>
      <img src = {TM}></img><br/>
         2018     (The Honey Badger)  .         .
              .
    </div>
    <font size = "5">  </font><br/>
    <QBvideo />
  </div>
  </div>
);

export default FScontent;


