const Card = ({ title, content }) => (
  <div className="card">
    <div className="header">{title}</div>
    <div className="content">
      {content}
    </div>
  </div>
);

const OLBcontent = () => (
  <div>
    <div className="header">  (Outside Linebacker) </div>
    <div className="content">
      {" "}
       . 
                    . 
         ,           . 
        3-4      .           , 
                      . 
                 .
    </div>
    <div className = "star">
      <font size = "5"> Star Player : Lawrence Taylor</font><br/>
      <img src = {LT}></img><br/>
    </div>
    <font size = "5">  </font><br/>
    <QBvideo />
  </div>
);

export default OLBcontent;


