const Card = ({ title, content }) => (
  <div className="card">
    <div className="header">{title}</div>
    <div className="content">
      {content}
    </div>
  </div>
);

const WRcontent = () => (
  <div>
    <div className="header">  (Wide Receiver) </div>
    <div className="content">
      {" "}
                   . 
                     , 
           . 
      -   , -   .
            , 
                       ,
                           . 
                 .
    </div>
    <div className = "star">
      <font size = "5"> Star Player : Kelvin Johnson</font><br/>
      <img src = {KJ}></img><br/>
    </div>
    <font size = "5">  </font><br/>
    <QBvideo />
  </div>
);

export default WRcontent;


