import React from "react";
import Popup from "reactjs-popup";
import QBvideo from "../video/QB";
import '../css/content.css';
import TB from "../image/TomBrady.jpeg";

const Card = ({ title, content }) => (
  <div className="card">
    <div className="header">{title}</div>
    <div className="content">
      {content}
    </div>
  </div>
);

const QBcontent = () => (
  <div>
    <div className="header"> (Quarter Back) </div>
    <div className="idiom">
      <font size = "5" color="FF00CC">Monday-morning quarterback</font><br/>
               <br/>
      <font size = "2.5" color="#ede734">
      Social media seems to have turned everyone into a Monday morning
      quarterback whenever political issues are discussed.
      <br/>
                  .
      </font>
    </div>
    <div className="content">
      {" "}
           .           .
           ,      .
            .
         
      <Popup
        trigger={<a><font color="FF00CC"></font></a>}
        position="bottom center"
        on="hover"
      >
        <Card title="" content = ""/>
      </Popup>
            
      <Popup
        trigger={<a><font color="FF00CC"></font></a>}
        position="bottom center"
        on="hover"
      >
        <Card title="" content=""/>
      </Popup>
                  .
                       ,
        1/4(quarter)        .
    </div>
    <div className = "star">
      <font size = "5"> Star Player : Tom Brady</font><br/>
      <img src = {TB}></img><br/>
      2001    2016     5 ()  .
      NFL -   .    16  15  ,
      12  ,  8   .     NFL   
                .
    </div>
    <font size = "5">  </font><br/>
    <QBvideo />
  </div>
);

export default QBcontent;



